.scrollbar{
    /**/
    &::-webkit-scrollbar {
        width: 2px;
    }
  
    /**/
    &::-webkit-scrollbar-thumb {
        border-radius: 150px;
        background: $secondary;
    }
  
    /**/
    &::-webkit-scrollbar-track {
        border-radius: 0;
        background: $gray200;
    }
}